import { Typography, makeStyles, Grid, Paper } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import Skeleton from "@material-ui/lab/Skeleton";
import LikeButton from "../LikeButton/LikeButton";
import Axios from "axios";
import ReactPlayer from "react-player";
// const style = {
//     width: 200,
//     height: 200,
//     borderRadius: 100,
//     objectFit: 'cover'
// }
const useStyles = makeStyles((theme) => ({}));

let apiKeys = ["k_tj93gztj", "k_dspqpo2c", "k_r51rig8u"];

const API_ADDRESS = "https://www.omdbapi.com/?apikey=e4c29baa&i=";
const MovieDetail = (props) => {
  const { movie, loading = false } = props;
  const classes = useStyles();
  const [movieDetail, setMovieDetail] = useState(movie);
  const [youtubeURL, setYoutubeURL] = useState(null);

  // console.log("MOVIEEE");
  // console.log(movieDetail);
  // console.log(movie.imdbId);

  const generateApiAddressForTrailer = async (i = 0) => {
    let API2_ADDRESS;

    if (i >= apiKeys.length) {
      return -1;
    }

    // console.log(apiKeys[i]);
    API2_ADDRESS =
      "https://imdb-api.com/en/API/YouTubeTrailer/" + apiKeys[i] + "/";

    try {
      let res = await fetch(`${API2_ADDRESS}${movie?.imdbId ?? movie?.imdbID}`);

      const data = await res.json();

      if (data?.errorMessage === "") {
        setYoutubeURL(data?.videoUrl);
      } else {
        generateApiAddressForTrailer(i + 1);
      }
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    //poster

    (async () => {
      let omdbRes;
      let posterPath;
      let updatedMovie;
      updatedMovie = movie;
      if (movie?.Poster) {
        posterPath = movie?.Poster;
      } else {
        try {
          omdbRes = await Axios.get(`${API_ADDRESS}${movie.imdbId}`);
        } catch (err) {
          console.log(err);
        }
        updatedMovie = { ...movie, ...omdbRes.data };

        try {
          const res = await Axios.get(
            "https://image.tmdb.org/t/p/w185" + movie.posterPath
          );
          if (res.data === "<h1>File not Found</h1>") throw Error;
          posterPath = "https://image.tmdb.org/t/p/w185" + movie.posterPath;
        } catch (err) {
          // console.log(res);
          posterPath = omdbRes?.data?.Poster ?? "Unknown";
        }
      }
      // console.log("updated movie");
      // updatedMovie.posterPath = posterPath;
      // console.log(updatedMovie);
      setMovieDetail(updatedMovie);
    })();
  }, [movie]);

  useEffect(() => {
    apiKeys = apiKeys.sort(() => Math.random() - 0.5);

    // (async () => {
    // let url;
    generateApiAddressForTrailer(0);
    // try {
    //   fetch(`${API2_ADDRESS}${movie?.imdbId ?? movie?.imdbID}`)
    //     .then((response) => response.json())
    //     .then((data) => {
    //       setYoutubeURL(data.videoUrl);
    //       console.log(data.videoUrl);
    //     });

    //   // const youtube = Axios.get(`${API2_ADDRESS}${movie.imdbId}`);
    //   // url = youtube.data.videoUrl;
    //   // console.log(url);
    // } catch (err) {
    //   console.log(err);
    // }
    // console.log(url);
    // setYoutubeURL(url);
    // })();
  }, [movie]);

  return (
    <Paper>
      <Grid container justify="center" alignItems="center">
        <Grid
          item
          container
          xs={12}
          justify="center"
          alignItems="center"
          style={{ marginTop: 10 }}
        >
          <Grid item>
            <Typography variant="h3">
              {movieDetail?.Title ?? movieDetail?.title ?? "Unknown"}
            </Typography>
          </Grid>
          <Grid item>
            <LikeButton
              index={movieDetail?.index ?? 0}
              height={40}
              width={40}
            />
          </Grid>
        </Grid>
        <Grid item container md={4} lg={2} alignItems="center" justify="center">
          {loading ? (
            <Skeleton animation="wave" variant="rect" />
          ) : (
            <Grid item style={{ padding: 20 }}>
              <img
                alt="Movie Poster"
                className={classes.media}
                src={movieDetail?.Poster ?? movieDetail?.posterPath}
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src =
                    "data:image/png;base64,";
                }}
              />
            </Grid>
          )}
        </Grid>

        <Grid
          container
          direction="row"
          item
          md={8}
          lg={10}
          style={{ padding: 10 }}
        >
          <Grid
            container
            item
            sm={5}
            md={3}
            alignItems="center"
            justify="center"
            direction="row"
            style={{ padding: 10 }}
          >
            {loading ? (
              <>
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  width="80%"
                  style={{ marginBottom: 6 }}
                />
              </>
            ) : (
              <Grid item>
                <Grid item>
                  <Typography variant="h6">Release Date:</Typography>
                  <Typography variant="body2">
                    {movieDetail?.Year ??
                      movieDetail?.release_date ??
                      "Unknown"}
                  </Typography>
                  <Grid item>
                    <Typography variant="h6">Runtime:</Typography>

                    <Typography variant="body2">
                      {movieDetail?.Runtime ?? "Unknown"}
                    </Typography>
                  </Grid>

                  <Grid item>
                    <Typography variant="h6">Genre:</Typography>
                    <Typography variant="body2">
                      {movieDetail?.Genre ?? "Unknown"}
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item container>
                  <Typography variant="h6">Ratings:</Typography>
                  {movieDetail?.Ratings?.map((Ratings, index) => {
                    return (
                      <Grid item xs={12} key={Ratings?.Source ?? index}>
                        <Typography variant="body2">
                          {Ratings?.Source ?? "Unknown"}:{" "}
                          {Ratings?.Value ?? index}
                        </Typography>
                      </Grid>
                    );
                  }) ?? (
                      <Grid item xs={12}>
                        <Typography variant="body2">
                          {movieDetail?.vote_average}
                        </Typography>
                      </Grid>
                    ) ?? (
                      <Grid item xs={12}>
                        <Typography variant="body2">"Unknown"</Typography>
                      </Grid>
                    )}
                </Grid>
              </Grid>
            )}
          </Grid>

          <Grid
            container
            item
            sm={7}
            md={4}
            alignItems="center"
            justify="center"
            style={{ padding: 10 }}
          >
            {loading ? (
              <Grid item>
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  width="80%"
                  style={{ marginBottom: 6 }}
                />
              </Grid>
            ) : (
              <Grid item container>
                <Grid item xs={12}>
                  <Typography variant="h6">Actors:</Typography>
                  <Typography variant="body2">
                    {movieDetail?.Actors ?? "Unknown"}
                  </Typography>
                </Grid>

                <Grid item xs={12}>
                  <Typography variant="h6">Box Office:</Typography>
                  <Typography variant="body2">
                    {movieDetail?.BoxOffice ?? "Unknown"}
                  </Typography>
                </Grid>

                <Grid item xs={12}>
                  <Typography variant="h6">Plot:</Typography>
                  <Typography variant="body2" align="justify">
                    {movieDetail?.Plot ?? movieDetail?.overview ?? "Unknown"}
                  </Typography>
                </Grid>
              </Grid>
            )}
          </Grid>

          <Grid container alignItems="center" justify="center" xs={12} md={5}>
            <Grid item xs={10}>
              <ReactPlayer
                url={youtubeURL}
                controls
                playbackRate={1}
                width="100%"
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default MovieDetail;
